body {
  margin: 0;
  padding: 0;
  font-family:"Courier New", Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(right, rgb(112, 228, 228), rgb(236, 98, 125));
  background: linear-gradient(to left, rgb(112, 228, 228), rgb(236, 98, 125));
}

a, a:focus, button, button:focus {
  cursor: pointer;
  outline:none;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.particles {
  position: fixed;
  top:0;
  left:0;
  bottom:0;
  right:0;
  z-index: -1;
}

.Tilt {
    display:inline-block;
}
.Tilt-inner {
    background: -webkit-linear-gradient(left, rgb(38, 44, 136),rgb(78, 78, 78));
    background: linear-gradient(to right, rgb(38, 44, 136),rgb(78, 78, 78));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
}
.form {
    width: 700px;
    background-image:
    -webkit-linear-gradient(325deg, rgba(206, 190, 41, .3) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .3) 71%, rgba(137, 180, 255, .3) 95%),
    -webkit-linear-gradient(178deg, rgba(206, 190, 41, .8) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .7) 71%, rgba(137, 180, 255, .7) 95%),
    -webkit-linear-gradient(67deg, rgba(206, 190, 41, 1) 0%, rgba(155, 31, 80, .6) 33%, rgba(41, 137, 216, 1) 71%, rgba(137, 180, 255, 1) 95%);
    background-image:
    linear-gradient(125deg, rgba(206, 190, 41, .3) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .3) 71%, rgba(137, 180, 255, .3) 95%),
    linear-gradient(-88deg, rgba(206, 190, 41, .8) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .7) 71%, rgba(137, 180, 255, .7) 95%),
    linear-gradient(23deg, rgba(206, 190, 41, 1) 0%, rgba(155, 31, 80, .6) 33%, rgba(41, 137, 216, 1) 71%, rgba(137, 180, 255, 1) 95%);
}
.imageContainer {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;

}
@media (max-width:1024px) {
    .imageContainer {
        display: block;
        position: absolute;
    }
}
.currentImage {
    width:auto;
    height:500px;
    display:none;
    opacity:0;
}
.visible {
    display:block;
    opacity:1;
}

.sk-cube-grid {
    width: 40px;
    height: 40px;
    margin: 100px auto;
  }
  
  .sk-cube-grid .sk-cube {
    width: 33%;
    height: 33%;
    background-color: #333;
    float: left;
    -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
            animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out; 
  }
  .sk-cube-grid .sk-cube1 {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; }
  .sk-cube-grid .sk-cube2 {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s; }
  .sk-cube-grid .sk-cube3 {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s; }
  .sk-cube-grid .sk-cube4 {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s; }
  .sk-cube-grid .sk-cube5 {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; }
  .sk-cube-grid .sk-cube6 {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s; }
  .sk-cube-grid .sk-cube7 {
    -webkit-animation-delay: 0s;
            animation-delay: 0s; }
  .sk-cube-grid .sk-cube8 {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s; }
  .sk-cube-grid .sk-cube9 {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; }
  
  @-webkit-keyframes sk-cubeGridScaleDelay {
    0%, 70%, 100% {
      -webkit-transform: scale3D(1, 1, 1);
              transform: scale3D(1, 1, 1);
    } 35% {
      -webkit-transform: scale3D(0, 0, 1);
              transform: scale3D(0, 0, 1); 
    }
  }
  
  @keyframes sk-cubeGridScaleDelay {
    0%, 70%, 100% {
      -webkit-transform: scale3D(1, 1, 1);
              transform: scale3D(1, 1, 1);
    } 35% {
      -webkit-transform: scale3D(0, 0, 1);
              transform: scale3D(0, 0, 1);
    } 
  }

@media (max-width: 600px) {
    .currentImage {
        width:90%;
        height:auto;
    }
}
.info {

    background: rgba(255, 255, 255, 0.2);
    height:100%;
    -webkit-transition: all 1s;
    transition: all 1s;
    -webkit-transition:opacity 2s forwards;
    transition:opacity 2s forwards;
    padding:10px;
    overflow: hidden;
}
@-webkit-keyframes opacity {
    100% {height:100%}
}
@keyframes opacity {
    100% {height:100%}
}
.info:hover {
    background: rgba(255, 255, 255, 0.603);
}
.info-paragraph {
    display: flex;
    justify-content: space-between;
    padding:2px;
    margin:0;
}

.detectedName {
    color:rgb(3, 4, 109);
    
}
.detectedValue {
    padding-left:2px;
}

@media (max-width:1024px) {
    .info {
        position: absolute;
        right:0;
        top:0; 
    }
    }
    @media (max-width:600px) {
        .info {
            margin:0 auto;
            position: static;
            width:90%;
            height:auto;
        }
    }
